    width: 110px;
    font-size: smaller !important;
    margin: 1em 0;
}

/* right image  */
.right img{
    width: 100%;
    height: 100%;
    opacity: 0.4;
    border-radius: 100px 80px 80px 90px;
    -webkit-animation-name: example;
            animation-name: example;
    -webkit-animation-duration: 8s;
            animation-duration: 8s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes example {
    0%   { opacity: 0.5;}
    25%  { opacity: 0.6;}
    50%  { opacity: 0.8;}
    75%  { opacity: 0.9;}
    85%   { opacity: 0.8;}
    95%  { opacity: 0.6;}
    100%  { opacity: 0.5;}
}

@keyframes example {
    0%   { opacity: 0.5;}
    25%  { opacity: 0.6;}
    50%  { opacity: 0.8;}
    75%  { opacity: 0.9;}
    85%   { opacity: 0.8;}
    95%  { opacity: 0.6;}
    100%  { opacity: 0.5;}
}


