.itemMessage__messages {
    border: 1px solid grey;
    background-color: rgb(191, 229, 241);
    border-radius: 0.2em 0.2em 0.2em 0.2em;
    max-height: 500px;
    height: 500px;
    overflow-y: auto;
    overflow-x: hidden;
}

.itemMessage__container {
    margin-bottom: 2em;
    text-align: end;
}

.itemMessage__compose {
    display: flex;
    justify-content: center;
}

.messages__loading {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}


.message {
    width: 100%;
    /* color: white; */
}

.message__sender {
    font-size: x-small;
    color:grey;
    overflow-wrap: break-word;
}

span {
    font-size: small;
    overflow-wrap: break-word;
}

.message__container {
    width: 100%;
    padding: 0.3em 1em;
    display: inline-block;
}

.user__message {
    padding: 0.5em 0.7em;
    max-width: 80%;
    border-radius: 0.8em 0.8em 0 0.8em;
    background-color: rgb(91, 196, 231);
    text-align: left !important;
    float: right;
}

.nonUser__message {
    padding: 0.5em 0.7em;
    max-width: 80%;
    border-radius: 0.8em 0.8em 0 0.8em;
    background-color: rgb(139, 209, 233);
    text-align: left !important;
    float: left;
}


@media(min-width: 500px){
    .message__sender {
        font-size: smaller;
    }
    
    span {
        font-size: medium;
    }
}

